
.container { 
  max-width: 80vw;
  max-height: 80vw;
  padding: 3rem;
  margin: 5rem auto;

  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-template-rows: 10rem 25vw;
  grid-template-areas: 
  'task-form task-list-pane'
  'task-grid task-list-pane';


  border: 1px solid black;
  border-radius: 10px;

  background: hsla(260, 5%, 50%, 1);

  background: linear-gradient(90deg, hsla(260, 5%, 50%, 1) 0%, hsla(258, 100%, 29%, 1) 30%);
  
  background: -moz-linear-gradient(90deg, hsla(260, 5%, 50%, 1) 0%, hsla(258, 100%, 29%, 1) 30%);
  
  background: -webkit-linear-gradient(90deg, hsla(260, 5%, 50%, 1) 0%, hsla(258, 100%, 29%, 1) 30%);
  

}

.task__container {
    animation: easeIn 0.8s ease;
    min-height: 50%;
}