@import '../abstracts/mixins';
@import '../themes/colors';

.task {
  &__heading {
    font-size: 2.4rem;
    text-transform: uppercase;

    // @include respond(med-desktop) {
    //   font-size: 1.8rem;
    // }
  }

  &__date {
    font-size: 2rem;

    // @include respond(med-desktop) {
    //   font-size: 1.8rem;
    // }
  }

  &__major {
    font-size: 2.2rem;
    font-weight: $medium;
    text-transform: uppercase;
  }
}

.save-or-discard {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  position: relative;
  margin-top: 2rem;
  margin-bottom: 2rem;

  .save-btn {
    background-color: #20ade9;
    color: #fff;
  }

  .edit-btn {
    position: absolute;
    background-color: #faf2ea;
    top: -0.1px;
    display: none;
  }

  .delete-btn {
    background-color: #997070;
    color: #fff;
  }
}

.form {
  // Styling for save or discard form
  &__save-or-discard {
    display: flex;
    flex-direction: column;
    // justify-content: space-evenly;

    // & * {
    //   @include align-labels;
    // }
  }

  // Styling for status btns container and buttons
  &__status-btns-box {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    margin-bottom: 2rem;

    @include respond(desktop) {
      flex-direction: column;
      flex-wrap: nowrap;
      row-gap: 1.2rem;
    }
  }

  &__radio-group {
    flex-basis: 23%;
  }

  &__input-radio {
    position: relative;
    top: 40%;
    display: none;
  }

  &__radio-label {
    &--incomplete,
    &--complete,
    &--in-progress {
      @include align-labels;
    }
  }

  // Create pseudo-element (better looking radio button)
  &__radio-btn {
    display: flex;

    @include respond(desktop) {
      justify-content: center;
    }
    &::after {
      content: '';
      display: flex;
      justify-content: center;
      align-items: center;
      height: 4rem;
      border: 1px solid black;
      border-radius: 5px;
      flex-basis: 100%;

      @include respond(desktop) {
        flex-basis: 85%;
      }

      @include respond(med-desktop) {
        height: 2.5rem;
      }
    }
  }

  // Insert text content within pseudo-elements
  &__radio-label--incomplete &__radio-btn::after {
    content: 'INCOMPLETE';
  }

  &__radio-label--in-progress &__radio-btn::after {
    content: 'IN PROGRESS';
  }

  &__radio-label--complete &__radio-btn::after {
    content: 'COMPLETE';
  }

  // When radio button in checked state, change background color of corresponding customized radio button
  &__input-radio:checked ~ &__radio-label--incomplete &__radio-btn::after {
    // background-color: #c0702a;
    background-color: #cfa493;
    color: white;
  }

  &__input-radio:checked ~ &__radio-label--in-progress &__radio-btn::after {
    // background-color: #e8de86;
    background-color: #cfc293;
    color: white;
  }

  &__input-radio:checked ~ &__radio-label--complete &__radio-btn::after {
    // background-color: #20ade9;
    background-color: #93cfc3;
    color: white;
  }

  // Styling for sub-task text input field
  &__sub-task__input-field {
    width: 85%;
    margin: 2rem 0;
    height: 5rem;
    border: 1px solid black;

    @include respond(desktop) {
      width: 85%;
      margin: 0 auto;
    }

    @include respond(med-desktop) {
      height: 3rem;
    }

    &:focus {
      outline: none;

      box-shadow: 1px 1px 2px 0.5px rgb(146, 146, 146, 0.7);
    }
  }
}

// Styling for sub-task <ul> and <li>'s
.subTask {
  &__container {
    columns: 100px 2;
  }
  &__list {
    list-style-type: none;
    // consider using a variable or extend
    font-size: 1.8rem;

    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__item {
    font-weight: $light;
    line-height: 1.4;
  }
}
