
.task { 
  &__square{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    align-content: space-between;
    gap: .5rem;
    padding:1rem;
    max-width: 20rem;
    max-height: 4rem;
    
    border-radius: 1rem;
    background: #fff;
  
    // calculate min-width and min-height after seeing behavior
  }

  &__date { 
    height: 1rem;
    margin: 0;

  }

  &__heading { 
    height: 1rem;
    margin: 0;
  }

  &__details { 
    max-width: 80%;
    height: 8rem;
    margin: 0;
    background-color: #fff;
  }
 
}

