.in-progress__container,
.complete__container { 
max-width: 20rem;
height: 30rem;
border-radius: 1rem;
padding: 1rem;
// background: hsla(264, 11%, 91%, 1);

// background: linear-gradient(225deg, hsla(264, 11%, 91%, 1) 51%, hsla(258, 31%, 69%, 1) 100%);

// background: -moz-linear-gradient(225deg, hsla(264, 11%, 91%, 1) 51%, hsla(258, 31%, 69%, 1) 100%);

// background: -webkit-linear-gradient(225deg, hsla(264, 11%, 91%, 1) 51%, hsla(258, 31%, 69%, 1) 100%);

background: linear-gradient(270deg, hsla(264, 11%, 91%, 1) 31%, hsla(258, 31%, 69%, 1) 100%);
background: -moz-linear-gradient(270deg, hsla(264, 11%, 91%, 1) 31%, hsla(258, 31%, 69%, 1) 100%);
background: -webkit-linear-gradient(270deg, hsla(264, 11%, 91%, 1) 31%, hsla(258, 31%, 69%, 1) 100%);

