.task__grid { 
  grid-area: task-grid;

  border: 1px solid black;
  display: grid;
  grid-template-columns: repeat(3,15vw);
  grid-template-rows: 5rem 35rem;

  gap: 2rem;
  padding: 1rem 1rem;
  overflow: scroll;

}

// tasks__squaresContainer for jsx className
.task__squares-container { 
  grid-column: 1 / -1;
  grid-row: 2 / -1;

  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(7rem, 15rem));
  grid-template-rows: repeat(auto-fit, minmax(8rem, 15rem));
  gap: 2.4rem;
  padding: 1rem 1rem;

}


