@import '../../abstracts/mixins';


@mixin btn-round {
  // Move these declarations to btn-round class in btn file
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  border: 1px solid black;
  width: 25%;
  height: 3rem;
  cursor: pointer;
  text-transform: uppercase;
}

.btn-round--large {
  width: 85%;
  height: 4rem;
  border-radius: 5px;
  font-size: 1.6rem;
  text-decoration: none;
  text-align: center;
  cursor: pointer;

  display: flex;
  justify-content: center;
  align-items: center;

  @include respond(med-desktop) {
    height: 2.5rem;
    font-size: 1.2rem;
  }
}

.btn-round--med {
  width: 85%;
}
